import React, {Component} from 'react';

export default class Faq extends Component {
    render = () => {
        return (
            <section id="viewAds" className="viewAds">
                <h1 className="titleForm">FAQ</h1>

                <div className="ads" id="faqContent">
                    <h3 className="faqTitle">Order tires from the site:</h3>
                    <p className="faqText">
                        1. Please choose size, brand, model and tire count.<br/>
                        2. Fill in the data for delivery (You need to be registered user).<br/>
                        3. Agree with the „Terms“ , when creating order.<br/>
                        4. When you click the button „Buy“, You confirm your order.<br/>
                        5. If everything is okay with your order and data, You will receive an email with information about Your order and the data you have entered.<br/>
                        *** If you don't receive email, perhaps You did not confirmed your order or You have entered wrong Email address!</p>

                    <h3 className="faqTitle">How to pay for an order:</h3>
                    <p className="faqText">1. You will pay your order when you receive it - directly on the courier.</p>

                    <h3 className="faqTitle">Confirming an order:</h3>
                    <p className="faqText">One of our employees will call you to confirm the order.<br/>
                        After you confirm the order, we will send it to you!<br/>
                        All orders which are created until 15:00 are processed the same day, orders created after 15:00 will be processed on the next day.</p>

                    <h3 className="faqTitle">Work time of the online shop:</h3>
                    <p className="faqText">Monday - Friday: 8:30 - 17:30.<br/>
                        *** Orders created on Saturday or Sunday are processed on Monday.</p>

                    <h3 className="faqTitle">Shipping time:​​</h3>
                    <p className="faqText">Weak season: 1-2 days<br/>
                        Strong season: 1-3 days<br/>
                        It is possible for an order to be shipped with delay, if it is delivered to a small town or village, where the orders are shipped once a week.<br/>
                        *** Tire Mania is not responsible for delays of the courier.
                    </p>

                    <h3 className="faqTitle">Shipping price:</h3>
                    <p className="faqText">3.60 EUR per tire<br/>
                        *** Shipping is made by courier company Speedy.<br/>
                        *** If you want to buy tires from any of our stores, do not order them through the website you have to go to that store.
                    </p>

                    <h3 className="faqTitle">Return policy:</h3>
                    <p className="faqText">Every order can be returned in 14 days.<br/>
                        If the receiver wants to return the delivery, he has to pay the transport prices unless returning damaged order.<br/>
                        Tires have to be returned with all of their documents.
                    </p>
                </div>
            </section>
        )
    }
}


