import React, { Component } from 'react';
import '../../styles/slideshow.css';

export default class Home extends Component {
    isLogged = () => {
        if (sessionStorage.getItem('username') && sessionStorage.getItem('authtoken')) {
            return true;
        }
        return false;
    };

    render = () => {
        return (
            <main>
                <section id="viewHome" className="viewHome">
                    <p className="site-title">Tire MANIA 2018</p>
                    <p className="site-desc">Online tire shop.</p>
                    {
                        !this.isLogged()
                            ? <p className="body-text">Please register or login to your account to access the catalog!</p>
                            : null
                    }
                    <article id="bg-img">

                    </article>
                </section>
            </main>
        )
    }
}


