import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import noProductsInCartImg from '../../images/noProducts.png';
import requester from '../../utils/requester';
import notificator from '../../utils/notificator';

export default class Cart extends Component {
    constructor(props) {
        super(props);

        this.state = {
            username: '',
            orderPrice: '0',
            cart: JSON.parse(localStorage.getItem('cart'))
        }
    }

    handleOrderPrice = () => {
        let orderPrice = 0;
        for (let i = 0; i < this.state.cart.length; i++) {
            let tire = this.state.cart[i];
            orderPrice += Number(tire.price) * Number (tire.count);
        }
        localStorage.setItem('orderPrice', orderPrice);
        return orderPrice;
    };

    cartIsNotEmpty = () => {
        if (this.state.cart && this.state.cart.length > 0) {
            return true;
        }
        return false;
    };

    //Product handling.
    handleRemoveItemFromCart = (event) => {
        let cart = JSON.parse(localStorage.getItem('cart'));
        let elementToBeRemoved = event.target.attributes.getNamedItem('data-id').value;

        for (let i = 0; i < cart.length; i++) {
            let currentElementId = cart[i]._id;
            if (currentElementId === elementToBeRemoved) {
                cart.splice(i, 1);
            }
        }

        localStorage.setItem('cart', JSON.stringify(cart));

        localStorage.setItem('endpoint', '/cart');
        localStorage.setItem('message', 'Removing product from cart..');

        if (JSON.parse(localStorage.getItem('cart')).length === 0) {
            localStorage.removeItem('cart');
            localStorage.removeItem('orderPrice');
        }
        this.props.history.push('/loading');
        notificator.showInfo('Successfully removed product from cart!');
    };

    handleSubmit = (event) => {
        event.preventDefault();
        let orderPriceValue = localStorage.getItem('orderPrice');
        let username = sessionStorage.getItem('username');

        let data = this.state;

        for (let i = 0; i < data.cart.length; i++) {
            data.cart[i].totalPrice = Number(data.cart[i].price) * Number(data.cart[i].count);
        }

        data.orderPrice = orderPriceValue;
        data.username = username;

        requester.post('appdata', 'orders', 'kinvey', data)
            .then((res) => {
                notificator.showInfo('Order confirmed! We will contact you later!');
                localStorage.setItem('endpoint', '/');
                localStorage.setItem('message', 'Processing your order!');
                this.props.history.push('/loading');
                localStorage.removeItem('cart');
                localStorage.removeItem('orderPrice')
            }).catch((res) => {
                notificator.showError(res.responseJSON.description);
            });
    };

    render = () => {
        const noProductsInCart = (
            <div className="noProductsPage">
                <h2>Cart is empty!</h2>
                <img src={noProductsInCartImg} title="No products in cart!" alt="No products in cart."/>
            </div>
        );

        return (
            <main>
                <section id="viewAds" className="viewAds">
                    <h1 className="titleForm">Your cart:</h1>
                    <div id="ads" className="cartAds">
                        {this.cartIsNotEmpty()
                            ? (
                                <div>
                                <ul>
                                    {this.state.cart.map(t => (
                                        <li key={t._id}>
                                            <div className="productActions"><button onClick={this.handleRemoveItemFromCart} className="deleteLink" data-id={t._id} title="Delete!">&#10006;</button></div>
                                            <Link to={"/details/" + t._id}>
                                                <div className="productPicture"><img src={t.picName} alt="Product in cart." height="200" /></div>
                                                <div className="productBrand">{t.brand}</div>
                                                <div className="productModel">{t.model}</div>
                                                <div className="productSize">{t.width}/{t.height} R{t.diameter}</div>
                                                <div className="productInfo">{t.season} {t.weightIndex}{t.speedIndex}</div>
                                                <div className="productCount">Count: {t.count}</div>
                                                <div className="productPrice">Sub-Total: {t.price * t.count}EUR.</div>
                                            </Link>
                                        </li>
                                    ))}
                                </ul>
                                    <div id="totalPrice" className="submitOrderForm">
                                        <div className="cartTotalPrice">Total price: {this.handleOrderPrice()}EUR.</div>
                                        <form onSubmit={this.handleSubmit}>
                                            <input type="submit" className="buyButton" value="Confirm Order!"/>
                                        </form>
                                    </div>
                                </div>
                            )
                            : noProductsInCart}
                    </div>
                </section>
            </main>
        )
    }
}


